.app {
  padding: 0;
  display: grid;
  grid-template-rows: auto 1fr;
  height: 100vh;
}

.page {
  padding: 10px;
  max-width: 1000px;
  flex-grow: 1;
}

.page-container {
  display: flex;
  overflow: scroll;
  justify-content: center;
}

img {
  width: 100%;
}

.question-answers {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: repeat(3, 1fr);
  justify-content: stretch;
  align-items: stretch;
  flex-wrap: wrap;
  margin: 2.5px;
}

.answer-text {
  width: 15em;
  text-align: center;
}

.answer {
  border: 1px solid black;
  margin: 2.5px;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: center;
}

sub {
  line-height: 0;
}

h1 {
  text-align: center;
}
/* tabs */
.tab-bar {
  display: flex;
  align-items: center;
  justify-content: stretch;
  /* position: sticky;
  top: 0; */
}

button.tab {
  border-radius: 0;
  flex-grow: 1;
  height: 3em;
}

button.tab[aria-pressed="true"] {
  border-style: inset;
}

.answered-question-bar {
  width: 100%;
  display: grid;
  gap: 5px;
  grid-template-columns: auto auto 1fr auto;
  align-items: center;
  justify-content: space-between;
}

.icon {
  width: 1em;
}

.upside-down {
  transform: rotate(-180deg);
}

.question-icon.right {
  color: darkgreen;
}

.question-icon.wrong {
  color: darkred;
}

.expand-icon {
  grid-column: 4;
  transition: all 200ms ease-in-out;
}

@media (max-width: 700px) {
  .question-answers {
    grid-template-columns: repeat(2, auto);
  }

  .answer-text {
    width: 10em;
    max-width: 35vw;
  }
}
